import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { AngularEditorConfig } from '@kolkov/angular-editor';
import { RestApiService } from 'src/app/services/rest-api.service';
import Swal from 'sweetalert2';
import { LandingPageModel, ValueUnitModel } from '../../../../models/dashboard';

@Component({
  selector: 'app-create',
  templateUrl: './create.component.html',
  styleUrls: ['./create.component.css']
})
export class CreateComponent implements OnInit {
  public documentData: LandingPageModel = new LandingPageModel();
  public documentId: number = 0;
  public isEdit: boolean = false;
  temp: {guidelines: string, faq: string} = {faq: '', guidelines: ''}
  public settings: {htop: boolean, ebottom: boolean, isEB: boolean, isGudelines: boolean, isFaq: boolean} = {htop:true, ebottom: false, isEB: false, isGudelines: false, isFaq: false};
  editorConfig: AngularEditorConfig = {
    editable: true,
    spellcheck: false,
    translate: 'no',
    sanitize: true,
    toolbarHiddenButtons: [
      ['toggleEditorMode', 'insertImage', 'insertVideo'],
      ['backgroundColorPicker', 'insertHorizontalRule']
    ]
  }
  constructor(
    private api: RestApiService,
    private aroute: ActivatedRoute,
    ) {
    this.createNewDocumentErrorHandler = this.createNewDocumentErrorHandler.bind(this);
  }

  onChangeGuidelines(event: any) {
    console.log(event);
    if (!event) {
      this.temp.guidelines = this.documentData.guidelines;
      this.documentData.guidelines = "";
    }
    else {
      this.documentData.guidelines = this.temp.guidelines
      this.temp.guidelines = "";
    }
  }

  onChangeFaq(event: any) {
    console.log(event);
    if (!event) {
      this.temp.faq = this.documentData.faq;
      this.documentData.faq = "";
    }
    else {
      this.documentData.faq = this.temp.faq
      this.temp.faq = "";
    }
  }

  async ngOnInit() {
    this.documentId = +this.aroute.snapshot.paramMap.get('id');
    if (!!this.documentId) {
      this.isEdit = true;
      this.documentData = await this.api.getAllLandingPagesWidthIdAdmin(this.documentId.toString()).toPromise();
      if (!!this.documentData.guidelines) {
        this.settings.isGudelines = true;
      }
      if (!!this.documentData.faq) {
        this.settings.isFaq = true;
      }
      console.log('edit');
    }
    else {
      this.isEdit = false;
      console.log('create');
    }
  }

  async submitForm() {
    console.log(this.documentData);
    if (!!this.isEdit) {
      let response = await this.api.updateDocumentAdmin(this.documentData, this.createNewDocumentErrorHandler).toPromise();
      this.swalEditAlert();
    }
    else {
    let response = await this.api.createNewDocumentAdmin(this.documentData, this.createNewDocumentErrorHandler).toPromise();
    this.swalDoneAlert();
    }

  }

  createNewDocumentErrorHandler(error: any) {
    console.log(error);
    this.swalErrorAlert(error);
  }


  swalDoneAlert() {
    Swal.fire({
      title: 'Done',
      text: "Document Added Successfully",
      icon: 'success',
    });
  }

  swalEditAlert() {
    Swal.fire({
      position: 'top-end',
      icon: 'success',
      title: 'Your work has been saved',
      showConfirmButton: false,
      timer: 1500
    })
  }

  swalErrorAlert(message: string) {
    Swal.fire({
      title: 'Error',
      text: message,
      icon: 'error',
    });
  }

  // removeFaq(i) {
  //   this.documentData.faq.splice(i, 1)
  // }

  removeMeta(i) {
    this.documentData.meta_data.splice(i, 1)
  }

  changeSpecialReq(event: any) {
    if (!!event) {
      this.documentData.requirements.eyesToBottom = new ValueUnitModel(this.documentData.requirements.hairToTop.value, this.documentData.requirements.hairToTop.unit);
      this.documentData.requirements.hairToTop = undefined;
    }
    else {
      this.documentData.requirements.hairToTop = new ValueUnitModel(this.documentData.requirements.eyesToBottom.value, this.documentData.requirements.eyesToBottom.unit);
      this.documentData.requirements.eyesToBottom = undefined;
    }
  }

  // enablePrintable() {
  //   this.documentData.requirements.isPrintable = true;
  //   this.documentData.requirements.isOnline = false;
  // }

  // enableDigital() {
  //   this.documentData.requirements.isPrintable = false;
  //   this.documentData.requirements.isOnline = true;
  // }

  // addFaq() {
  //   this.documentData.faq.push({q: '', a: ''})
  // }

  addMeta() {
    // if (this.documentData.meta_data?.length === 0  ) {
    //   this.documentData.meta_data = [];
    // }
    this.documentData.meta_data.push({name: '', content: ''})
  }

  onChangeData() {

  }

}
