import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LandingPageComponent } from './components/landing-page/landing-page.component';
import { ProcessingPageComponent } from './components/processing-page/processing-page.component';
import { ControlComponent } from './components/Control/Control.component';
import { CreateComponent } from './components/Control/Documents/create/create.component';
import { ListComponent } from './components/Control/Documents/list/list.component';
import { GuidelinesPageComponent } from './components/guidelines-page/guidelines-page.component';
import { SettingsComponent } from './components/Control/System/settings/settings.component';

const routes: Routes = [
  {path: '', component: LandingPageComponent, data: {animation: 'Landing'}},
  {path: 'document/:slug', component: LandingPageComponent, data: {animation: 'Landing'}},
  {path: 'guidelines/:slug', component: GuidelinesPageComponent, data: {animation: 'Landing'}},
  {path: 'result', component: ProcessingPageComponent},
  {path: 'admin', component: ControlComponent, children: [
    {path: 'document/list', component: ListComponent},
    {path: 'document/settings', component: SettingsComponent},
    {path: 'document/create', component: CreateComponent},
    {path: 'document/edit/:id', component: CreateComponent},
  ]}
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {
    initialNavigation: 'enabled'
})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
