export class LandingPageModel {
    constructor(
      public id: number = undefined,
      public title?: string,
      public country?: string,
      public imageUrl: string= "",
      public requirements: DocumentRequirementsModel = new DocumentRequirementsModel(),
      public html1: string = "",
      public detailed_img_url: string = "",
      public meta_data: {
        name: string,
        content: string
      }[] = [],
      public faq: string = "",
      public html2: string = "",
      public guidelines: string = "",
      public status: string = "published",
      public updatedAt?: string,
    ){}
}

export class DocumentRequirementsModel {
constructor(
    public dpi: number = 300,
    public isOnline: boolean = false,
    public isPrintable: boolean = false,
    public bgColor: string = "#ffffff",
    public width: ValueUnitModel = new ValueUnitModel(),
    public height: ValueUnitModel = new ValueUnitModel(),
    public headHeight:ValueUnitModel = new ValueUnitModel(),
    public hairToTop: ValueUnitModel = new ValueUnitModel(),
    public eyesToBottom: ValueUnitModel = undefined,
){}
}

export class ValueUnitModel {
  constructor(
    public value: number = 0,
    public unit: string = 'in'
  ){}
}
export class LandingPageListModel {
  constructor(
    public id: number,
    public title: string,
    public country: string,
    public imageUrl: string
  ){}
}
