import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { retry, catchError, map } from 'rxjs/operators';
import { LandingPageModel } from '../models/dashboard';

@Injectable({
  providedIn: 'root'
})
export class RestApiService {

  // Define API
  apiBase = 'https://fathomless-bastion-96634.herokuapp.com/api';
  apiURL = 'https://sdk.photoroom.com/v1';
  apiKey = 'fd24a7481f4d27d6741052516e4b671eb2c83216';

  constructor(private http: HttpClient) { }

  // Http Options
  private headers = new HttpHeaders({ 'Content-Type': 'application/json' });



    // Admin Endpoints
    getAllLandingPagesAdmin(errorHandler: any = undefined){
      return this.get('admin-control', errorHandler);
    }

    getAllSettingsAdmin(errorHandler: any = undefined){
      return this.get('admin-control/settings', errorHandler);
    }

    updateSettingAdmin(reqBody: {key: string, value: string},errorHandler: any = undefined){
      return this.put('admin-control/settings',{}, reqBody, errorHandler);
    }

    updateBulkSettingAdmin(settings: {key: string, value: string}[],errorHandler: any = undefined){
      return this.put('admin-control/all-settings',{}, {settings}, errorHandler);
    }

    getAllLandingPagesWidthIdAdmin(documentId: string, errorHandler: any = undefined){
      return this.get('admin-control/' + documentId.toString(), errorHandler);
    }

    createNewDocumentAdmin(documentData ,errorHandler: any = undefined){
      return this.post('admin-control', this.headers, documentData, errorHandler);
    }

    updateDocumentAdmin(documentData: LandingPageModel ,errorHandler: any = undefined){
      return this.put('admin-control/'+ documentData.id.toString(),{}, documentData, errorHandler);
    }

    deleteLandingPageWithIdAdmin(documentId: number, errorHandler: any = undefined) {
      return this.delete('admin-control/' + documentId.toString(), errorHandler);
    }

    // User Endpoints
    getAllSettings(errorHandler: any = undefined){
      return this.get('settings', errorHandler);
    }
    cropImage(formData: any, docID: number ,errorHandler: any = undefined) : Observable<any> {
      return this.post('upload/'+ docID.toString(), {} ,formData, errorHandler);
    }


    getAllLandingPagesList(errorHandler: any = undefined){
      return this.get('documents/list', errorHandler);
    }


    getAllLandingPages(errorHandler: any = undefined){
      return this.get('documents', errorHandler);
    }

    getLandingPageById(id: number, errorHandler: any = undefined) : Observable<any> {
      return this.get('documents/' + id.toString(), errorHandler);
    }

  private post(path: string, headers = null ,params: any, errorHandler: any = undefined) {
    return this.call('post', path, headers, params, errorHandler);
  }

  private patch(path: string, params: any, errorHandler: any = undefined) {
    return this.call('patch', path, params, errorHandler);
  }

  private get(path: string, errorHandler: any = undefined) {
    return this.call('get', path, {},{}, errorHandler);
  }


  private put(path: string, headers = null, params: any, errorHandler: any = undefined) {
    return this.call('put', path, headers, params, errorHandler);
  }

  private delete(path: string, errorHandler: any = undefined) {
    return this.call('delete', path, {}, {}, errorHandler);
  }

  private download(
    path: string,
    params: any = undefined,
    errorHandler: any = undefined
  ) {
    if (!errorHandler) errorHandler = this.handleError;
    return this.http
      .get<any>(`${this.apiBase}/${path}`, {
        params,
        headers: this.headers,
        responseType: 'blob' as 'json',
      })
      .pipe(catchError(errorHandler));
  }


  callImage(method, path: string, headers: any, params: any, errorHandler: any = undefined) {
    if (!errorHandler) errorHandler = this.handleError;
    return this.http
      .request<any>(method, `${this.apiBase}/${path}`, {
        body: params,
        headers: !!headers ? headers : this.headers,
        responseType: 'blob' as 'json',
      })
      .pipe(
        map((resp) => {
          if (resp.error) {
            console.error('Error', resp);
            throw resp.error;
          }
          return resp;
        }),
        catchError(errorHandler)
      );
  }

  call(method, path: string, headers: any, params: any, errorHandler: any = undefined) {
    if (!errorHandler) errorHandler = this.handleError;
    return this.http
      .request<any>(method, `${this.apiBase}/${path}`, {
        body: params,
        headers: !!headers ? headers : this.headers,
      })
      .pipe(
        map((resp) => {
          if (resp.error) {
            console.error('Error', resp);
            throw resp.error;
          }
          return resp;
        }),
        catchError(errorHandler)
      );
  }

   // Error handling
   handleError(error) {
    let errorMessage = '';
    if(error.error instanceof ErrorEvent) {
      // Get client-side error
      errorMessage = error.error.message;
    } else {
      // Get server-side error
      errorMessage = `Error Code: ${error.status}\nMessage: ${error.message}`;
    }
    console.log(errorMessage);
    return throwError(errorMessage);
 }
}
