import { HttpErrorResponse } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { LandingPageModel } from 'src/app/models/dashboard';
import { DataManagementService } from 'src/app/services/data-management.service';
import { RestApiService } from 'src/app/services/rest-api.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.css']
})
export class ListComponent implements OnInit {
  landingPagesData: LandingPageModel[] = [];
  searchWord: string = "";
  settings: {key: string, value: string}[] = [];
  settingsIndexes: {primaryColor: number, defaultPage: number} = {primaryColor: null, defaultPage: null};
  constructor(
    private api: RestApiService,
    public dataManagementService: DataManagementService,
  ) {
    this.ListErrorHandler = this.ListErrorHandler.bind(this);
    this.setAsDefaultErrorHandler = this.setAsDefaultErrorHandler.bind(this);
    this.deleteErrorHandler = this.deleteErrorHandler.bind(this);
  }

  async ngOnInit() {
    await this.getAllSettings();
    await this.getAllLandingPages();
  }


  async getAllSettings() {
    this.settings = await this.api.getAllSettingsAdmin().toPromise();
    this.settingsIndexes.defaultPage = this.settings.findIndex(a=> a.key === 'defaultPageId');
    console.log(this.settingsIndexes)

  }

  setDocumentAsDefault (documentId: string) {

    Swal.fire({
      title: 'Are you sure?',
      text: "You want to set this document as default!",
      icon: 'info',
      showCancelButton: true,
      customClass: {
        confirmButton: 'btn-info',
        cancelButton: 'btn-secondary'
      },
      confirmButtonText: 'Yes, do it!'
    }).then(async (result) => {
      if (result.isConfirmed) {
        await this.api.updateSettingAdmin({key:"defaultPageId", value: documentId.toString()}, this.setAsDefaultErrorHandler).toPromise();
        await this.getAllLandingPages();
        await this.getAllSettings();
        await this.dataManagementService.getAllSettings();
        Swal.fire(
          {
            title: 'Done!',
            text: 'Your document has been set as default.',
            icon: 'success',
            customClass: {
              confirmButton: 'btn-primary',
              cancelButton: 'btn-primary'
            },
          }
        )
      }
    })
  }

  deleteDialogAlert (documentId: number) {

    Swal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'error',
      showCancelButton: true,
      customClass: {
        confirmButton: 'btn-danger',
        cancelButton: 'btn-secondary'
      },
      confirmButtonColor: '#d33',
      confirmButtonText: 'Yes, delete it!'
    }).then(async (result) => {
      if (result.isConfirmed) {
        await this.api.deleteLandingPageWithIdAdmin(documentId, this.deleteErrorHandler).toPromise();
        await this.getAllLandingPages();
        Swal.fire(
          {
            title: 'Deleted!',
            text: 'Your file has been deleted.',
            icon: 'success',
            customClass: {
              confirmButton: 'btn-primary',
              cancelButton: 'btn-primary'
            },
          }
        )
      }
    })
  }

  deleteErrorHandler(error:HttpErrorResponse) {
    console.log(error.error)
    Swal.fire(
      {
        title: 'You Cannot Delete The Default!',
        text: error.error.message,
        icon: 'warning',
        customClass: {
          confirmButton: 'btn-primary',
          cancelButton: 'btn-primary'
        },
      }
    )
  }

  setAsDefaultErrorHandler() {

  }
  ListErrorHandler() {

  }

  async getAllLandingPages() {
    this.dataManagementService.startLoadingData();
    this.landingPagesData = await this.api.getAllLandingPagesAdmin(this.ListErrorHandler).toPromise();
    this.dataManagementService.stopLoadingData();
  }


}
