import { Injectable } from '@angular/core';
import { DomSanitizer, Meta, MetaDefinition, Title } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { LandingPageListModel, LandingPageModel } from '../models/dashboard';
import { RestApiService } from './rest-api.service';
import Swal from 'sweetalert2';

@Injectable({
  providedIn: 'root'
})
export class DataManagementService {
  landingAnimation = false;
  loading = false;
  loadingData = true;
  processingResponse: {image: string, details: string} = {image: '', details: ''}
  imgURL: any;
  landingPageData: LandingPageModel;
  landingPageList: LandingPageListModel[];
  landingPagesData: LandingPageModel[];
  metaData: {name: string, content: string}[] = []
  settings: {primaryColor: string, defaultPageId: string} = {primaryColor: '#42d2ef', defaultPageId: null}

  constructor(
    private router: Router,
    private api: RestApiService,
    private sanitizer: DomSanitizer,
    private titleService: Title,
    private metaTagService: Meta,
  ) {
    this.getLandingPageDataErrorHandler = this.getLandingPageDataErrorHandler.bind(this);
  }


  async getAllSettings() {
    const allSettings: {key: string, value: string}[] = await this.api.getAllSettings().toPromise();
    let primaryColor = allSettings.find(a=> a.key === 'primaryColor');
    this.settings.primaryColor = primaryColor.value;
    let defaultPageId = allSettings.find(a=> a.key === 'defaultPageId');
    this.settings.defaultPageId = defaultPageId.value;
    console.log(this.settings)
  }




  viewMyImageDetails (img: string = "assets/images/passport.png") {
    Swal.fire({
      title: 'Detailed photo criteria',
      showConfirmButton: false,
      html: `
      <div class="row col-12 mx-0">
        <div class="col-5">
        <img src="${img}" class="w-100">
        </div>
        <div class="col-7 text-left">
        <h4 class="mb-2">
Compatibility test <strong class="text-success">passed</strong> <span class="ml-2"></span>
</h4>
        <ul class="check-list mb-4">
       <li>Face is recognized</li>
       <li>Only one face is allowed</li>
       <li>Minimum dimension</li>
       <li>Correct photo proportions<sup class="ml-1 text-warning"></sup></li>
       <li>Correct photo orientation</li>
       <li>Face occupied 30% to 50% of the photo area<sup class="ml-1 text-warning"></sup></li>
       <li>No red eyes</li>
       <li>Can remove background</li>
       </ul>
       <sup class="ml-1 text-warning"></sup> automatically corrected
        </div>
      </div>
      `,
      customClass:{title: 'text-left',popup: 'swal-popup-w', htmlContainer:'mx-0'}

    })
  }

  async viewLandingPage(id: number) {
    console.log(id)
    this.startLoadingData();

    this.landingPageData = await this.api.getLandingPageById(id, this.getLandingPageDataErrorHandler).toPromise();
    if (!!this.landingPageData) {
      this.setTitleAndMeta(this.landingPageData.title, this.landingPageData.meta_data);
    }
    else {
      await this.getAllLandingPages();
      this.router.navigate(['/']);
      this.stopLoadingData();
    }
    this.stopLoadingData();
  }

  async getLandingPageDataErrorHandler (error: any) {

  }

  setTitleAndMeta (title: string, meta: {name: string, content: string}[]) {
    this.titleService.setTitle('Passport App - ' + title);
    this.metaTagService.addTags(meta);
  }
  startLoadingData() {
    this.loadingData = true;
  }

  stopLoadingData() {
    this.loadingData = false;
  }

  startLoading() {
    this.loading = true;
  }

  stopLoading() {
    this.loading = false;
  }

  async removeBackground(formData: FormData, docID: number) {
    this.startLoading();
    let Imageresp: {image: string, details: string} = await this.api.cropImage(formData, docID,this.removeBackgroundErrorHandler).toPromise();

      // const imageObjectURL = URL.createObjectURL(imageBlob);
      // this.dataManagementService.processingResponse.image = this.sanitizer.bypassSecurityTrustResourceUrl(imageObjectURL);
      this.processingResponse = Imageresp;
      this.stopLoading()

    return this.processingResponse;
  }

  swalErrorAlert(message: string) {
    Swal.fire({
      title: 'Error',
      text: message,
      icon: 'error',
    });
  }
  removeBackgroundErrorHandler = (error: any) => {
    this.stopLoading();
    let message = "Something Went Wrong!";
    switch (error.status) {
      case 400:
      message = "BAD_REQUEST"
        break;
      case 404:
        message = "NOT_FOUND"
      break;
      case 500:
        message = "INTERNAL_SERVER"
        break;
      case 401:
        message = "FACE_TO_CLOSE"
      break;
      case 402:
        message = "NO_FACE_DETECTED"
        break;
      case 403:
        message = "UPLOAD_ERROR"
      break;
      case 405:
        message = "PHOTOROOM_ERROR"
        break;
      case 406:
        message = "JIMP_ERROR"
      break;
      default:
        break;
    }
    this.router.navigate(['/']);
    this.swalErrorAlert(message);
  }

  async getAllLandingPagesList() {
    this.startLoadingData();
    this.landingPageList = await this.api.getAllLandingPagesList().toPromise();
    this.stopLoadingData();
  }

  async getAllLandingPages() {
    this.startLoadingData();
    const LandingPages = await this.api.getAllLandingPages().toPromise();
    this.landingPagesData = LandingPages;
    this.landingPageData = this.landingPagesData[0];
    console.log(this.landingPageData);
    this.stopLoadingData();
  }

}
